/* 基础样式与颜色变量 */
:root {
  --primary-color: #6c5ce7;
  --secondary-color: #8e44ad;
  --accent-color: #00cec9;
  --background-color: #121424;
  --card-bg: rgba(30, 39, 70, 0.8);
  --text-color: #ecf0f1;
  --text-muted: #b2bec3;
  --success-color: #00b894;
  --error-color: #d63031;
  --border-radius: 12px;
  --box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
  --gradient-primary: linear-gradient(135deg, #6c5ce7, #8e44ad);
  --gradient-secondary: linear-gradient(135deg, #00cec9, #0984e3);
  --transition: all 0.3s ease;
}

/* 全局重置和基础样式 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', 'Segoe UI', 'Helvetica Neue', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  line-height: 1.6;
  overflow-x: hidden;
}

button {
  cursor: pointer;
  font-family: inherit;
  border: none;
  outline: none;
  transition: var(--transition);
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* 容器样式 */
.app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

/* 背景装饰 */
.bg-decoration {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 90% 10%, rgba(108, 92, 231, 0.2), transparent 40%),
              radial-gradient(circle at 10% 90%, rgba(0, 206, 201, 0.15), transparent 40%);
  z-index: -1;
}

.bg-cube {
  position: fixed;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.03);
  animation: float 15s infinite ease-in-out;
  z-index: -1;
}

.cube1 {
  width: 300px;
  height: 300px;
  top: -50px;
  right: -100px;
  transform: rotate(45deg);
  animation-delay: 0s;
}

.cube2 {
  width: 200px;
  height: 200px;
  bottom: 100px;
  left: -50px;
  transform: rotate(20deg);
  animation-delay: -5s;
}

.cube3 {
  width: 150px;
  height: 150px;
  bottom: -50px;
  right: 30%;
  transform: rotate(60deg);
  animation-delay: -10s;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(45deg);
  }
  50% {
    transform: translateY(-30px) rotate(65deg);
  }
}

/* 头部样式 */
.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: rgba(18, 20, 36, 0.8);
  backdrop-filter: blur(10px);
  position: sticky;
  top: 0;
  z-index: 100;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.logo-icon {
  width: 36px;
  height: 36px;
  fill: none;
  stroke: var(--accent-color);
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.logo h1 {
  font-size: 1.5rem;
  font-weight: 700;
  background: var(--gradient-secondary);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

/* 钱包连接按钮样式 */
.connect-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--gradient-primary);
  color: white;
  padding: 0.75rem 1.25rem;
  border-radius: var(--border-radius);
  font-weight: 600;
  box-shadow: 0 4px 15px rgba(108, 92, 231, 0.3);
  transition: var(--transition);
}

.connect-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(108, 92, 231, 0.4);
}

.connect-btn:active:not(:disabled) {
  transform: translateY(1px);
}

.connect-btn.lg {
  padding: 1rem 1.5rem;
  font-size: 1.1rem;
}

.wallet-icon, .wallet-icon-sm {
  width: 20px;
  height: 20px;
  fill: white;
}

.wallet-icon-sm {
  width: 16px;
  height: 16px;
}

.account-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.account-badge, .country-badge {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 30px;
  font-size: 0.9rem;
  color: var(--text-color);
}

.account-badge {
  background: rgba(108, 92, 231, 0.2);
  border: 1px solid rgba(108, 92, 231, 0.3);
}

.country-badge {
  background: rgba(0, 206, 201, 0.2);
  border: 1px solid rgba(0, 206, 201, 0.3);
}

/* 加载状态样式 */
.spinner {
  width: 20px;
  height: 20px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s linear infinite;
  display: inline-block;
  margin-right: 0.5rem;
}

.spinner.sm {
  width: 16px;
  height: 16px;
  border-width: 2px;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* 主内容区域样式 */
.main-content {
  flex: 1;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 2rem 0.4rem;
}

/* 连接提示样式 */
.connect-prompt {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 3rem 1rem;
  background: var(--card-bg);
  border-radius: var(--border-radius);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: var(--box-shadow);
  max-width: 600px;
  margin: 4rem auto;
}

.connect-illustration {
  width: 100px;
  height: 100px;
  margin-bottom: 1.5rem;
  stroke: var(--accent-color);
  fill: none;
  stroke-width: 1.5;
  stroke-linecap: round;
  stroke-linejoin: round;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.connect-prompt h2 {
  font-size: 1.75rem;
  margin-bottom: 1rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.connect-prompt p {
  color: var(--text-muted);
  margin-bottom: 2rem;
  max-width: 400px;
}

/* 彩票概览区域 */
.lottery-overview {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

/* 优化后的prize-card样式 */
.prize-card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  box-shadow: var(--box-shadow);
  border: 1px solid rgba(255, 255, 255, 0.05);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  position: relative;
  overflow: hidden;
  background-image: 
    radial-gradient(circle at 90% 10%, rgba(108, 92, 231, 0.1), transparent 40%),
    radial-gradient(circle at 10% 90%, rgba(0, 206, 201, 0.08), transparent 40%);
  transition: var(--transition);
}

.prize-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.4);
  border-color: rgba(255, 255, 255, 0.1);
}

.prize-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: var(--gradient-primary);
}

.prize-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.prize-card-header h2 {
  font-size: 1.25rem;
  color: var(--text-muted);
  margin-bottom: 0;
  position: relative;
  padding-left: 0.8rem;
}

.prize-card-header h2::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 80%;
  background: var(--accent-color);
  border-radius: 2px;
}

.prize-card-decoration {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.5;
}

.decoration-icon {
  width: 30px;
  height: 30px;
  fill: var(--accent-color);
  opacity: 0.6;
}

.prize-amount-wrapper {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.prize-amount {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 80px;
}

.prize-value-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.8rem 1.5rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.prize-currency {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: rgba(0, 206, 201, 0.1);
  border-radius: 12px;
  border: 1px solid rgba(0, 206, 201, 0.2);
}

.eth-icon {
  font-size: 1.75rem;
  color: var(--accent-color);
  display: inline-block;
  text-shadow: 0 0 5px rgba(0, 206, 201, 0.5);
}

.prize-value {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.amount {
  font-size: 2.5rem;
  font-weight: 700;
  background: var(--gradient-secondary);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  line-height: 1;
}

.amount-label {
  font-size: 0.9rem;
  color: var(--text-muted);
  margin-left: 2px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.refresh-btn {
  background: transparent;
  color: var(--accent-color);
  padding: 0.75rem;
  border-radius: 50%;
  transition: var(--transition);
  border: 1px dashed rgba(0, 206, 201, 0.3);
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.refresh-btn:hover:not(:disabled) {
  background: rgba(0, 206, 201, 0.1);
  transform: rotate(30deg);
  border-style: solid;
}

.refresh-icon {
  width: 24px;
  height: 24px;
  fill: var(--accent-color);
}

.prize-info {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 0 0.5rem;
}

.prize-detail {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.85rem;
  color: var(--text-muted);
  background: rgba(255, 255, 255, 0.02);
  padding: 0.4rem 0.8rem;
  border-radius: 20px;
  transition: var(--transition);
}

.prize-detail:hover {
  background: rgba(255, 255, 255, 0.05);
}

.prize-detail-icon {
  width: 16px;
  height: 16px;
  fill: var(--primary-color);
  opacity: 0.8;
}

.prize-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.prize-card-action {
  flex: 1;
}

.prize-action-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  background: var(--gradient-primary);
  color: white;
  padding: 0.8rem 1.25rem;
  border-radius: var(--border-radius);
  font-weight: 600;
  width: 100%;
  transition: var(--transition);
  box-shadow: 0 4px 15px rgba(108, 92, 231, 0.3);
}

.prize-action-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(108, 92, 231, 0.4);
}

.prize-action-btn:active:not(:disabled) {
  transform: translateY(1px);
}

.prize-action-btn.disabled {
  opacity: 0.5;
  cursor: not-allowed;
  background: linear-gradient(135deg, #a29bfe, #6c5ce7);
}

.action-icon {
  width: 18px;
  height: 18px;
  fill: white;
}

.prize-card-decoration.bottom {
  position: absolute;
  right: 0;
  bottom: 0;
  opacity: 0.2;
}

.decoration-pulse {
  width: 100px;
  height: 100px;
  background: radial-gradient(circle, rgba(108, 92, 231, 0.8) 0%, rgba(108, 92, 231, 0) 70%);
  border-radius: 50%;
  transform: translate(30%, 30%);
  animation: pulse-decoration 3s infinite ease-in-out;
}

@keyframes pulse-decoration {
  0% {
    transform: translate(30%, 30%) scale(0.8);
    opacity: 0.5;
  }
  50% {
    transform: translate(30%, 30%) scale(1);
    opacity: 0.3;
  }
  100% {
    transform: translate(30%, 30%) scale(0.8);
    opacity: 0.5;
  }
}

/* 移动端适配 */
@media (max-width: 768px) {
  .prize-value-container {
    padding: 0.6rem 1rem;
  }
  
  .prize-currency {
    width: 40px;
    height: 40px;
  }
  
  .eth-icon {
    font-size: 1.5rem;
  }
  
  .amount {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .prize-card {
    padding: 1.25rem;
  }
  
  .prize-card-header h2 {
    font-size: 1.1rem;
  }
  
  .prize-amount-wrapper {
    gap: 1rem;
  }
  
  .prize-value-container {
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 1rem;
  }
  
  .prize-value {
    align-items: center;
  }
  
  .prize-info {
    justify-content: center;
  }
}

/* 彩票状态区更新样式 */
.lottery-status {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  box-shadow: var(--box-shadow);
  border: 1px solid rgba(255, 255, 255, 0.05);
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.status-item {
  display: flex;
  align-items: flex-start;
  gap: 1.25rem;
  position: relative;
  background: rgba(255, 255, 255, 0.03);
  border-radius: var(--border-radius);
  padding: 1.25rem;
  transition: var(--transition);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.status-item:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: translateY(-2px);
}

.status-icon-wrapper {
  position: relative;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.status-icon {
  width: 24px;
  height: 24px;
  fill: var(--accent-color);
  position: relative;
  z-index: 2;
}

.status-icon-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 206, 201, 0.1);
  border-radius: 12px;
  transform: rotate(45deg);
  z-index: 1;
  transition: var(--transition);
}

.status-item:hover .status-icon-bg {
  background: rgba(0, 206, 201, 0.2);
  transform: rotate(0deg);
}

.status-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.status-info h3 {
  font-size: 1rem;
  color: var(--text-muted);
  margin-bottom: 0;
  letter-spacing: 0.5px;
  font-weight: 600;
}

.status-value-container {
  display: flex;
  align-items: baseline;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.status-value {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
}

.highlight-value {
  font-size: 1.75rem;
  font-weight: 700;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.status-label {
  font-size: 0.85rem;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  opacity: 0.8;
}

.status-progress-bar {
  width: 100%;
  height: 6px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
  overflow: hidden;
  margin-top: 0.5rem;
}

.status-progress {
  height: 100%;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
  border-radius: 3px;
  transition: width 0.5s ease-out;
}

.lottery-status-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.status-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.status-dot::after {
  content: "";
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  border-radius: 50%;
  animation: pulse-ring 1.5s infinite;
}

.dot-active {
  background-color: var(--success-color);
}

.dot-active::after {
  border: 2px solid rgba(0, 184, 148, 0.3);
  animation: pulse-ring 1.5s infinite;
}

.dot-inactive {
  background-color: var(--error-color);
}

.dot-inactive::after {
  border: 2px solid rgba(214, 48, 49, 0.3);
  animation: none;
}

@keyframes pulse-ring {
  0% {
    transform: scale(0.95);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.5;
  }
  100% {
    transform: scale(0.95);
    opacity: 1;
  }
}

.lottery-status-message {
  font-size: 0.9rem;
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
  display: inline-block;
  margin-top: 0.5rem;
}

.lottery-open-message {
  color: var(--success-color);
  background-color: rgba(0, 184, 148, 0.1);
  border-left: 3px solid var(--success-color);
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
  display: inline-block;
}

.lottery-closed-message {
  color: var(--error-color);
  background-color: rgba(214, 48, 49, 0.1);
  border-left: 3px solid var(--error-color);
  padding: 0.5rem 0.75rem;
  border-radius: 4px;
  display: inline-block;
}

.status-success {
  color: var(--success-color);
}

.status-error {
  color: var(--error-color);
}

/* 彩票操作区域 */
.lottery-actions {
  margin-bottom: 2rem;
}

.action-card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  box-shadow: var(--box-shadow);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.action-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.action-header h2 {
  font-size: 1.25rem;
  color: var(--text-color);
}

.price-tag {
  background: rgba(255, 255, 255, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 30px;
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--accent-color);
}

.action-btn {
  width: 100%;
  padding: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: var(--border-radius);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  transition: var(--transition);
}

.buy-btn {
  background: var(--gradient-secondary);
  color: white;
  margin-bottom: 1rem;
  box-shadow: 0 4px 15px rgba(0, 206, 201, 0.3);
}

.buy-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 206, 201, 0.4);
}

.ticket-icon {
  width: 24px;
  height: 24px;
  fill: white;
}

.message {
  padding: 1rem;
  border-radius: var(--border-radius);
  font-size: 0.9rem;
  text-align: center;
}

.message.success {
  background: rgba(0, 184, 148, 0.2);
  color: var(--success-color);
  border: 1px solid rgba(0, 184, 148, 0.3);
}

.message.error {
  background: rgba(214, 48, 49, 0.2);
  color: var(--error-color);
  border: 1px solid rgba(214, 48, 49, 0.3);
}

/* 彩票详情区域 */
.lottery-details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

.card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  box-shadow: var(--box-shadow);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.card h2 {
  font-size: 1.25rem;
  margin-bottom: 1.5rem;
  color: var(--text-muted);
}

.timeline {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.timeline-item {
  display: flex;
  gap: 1rem;
  position: relative;
}

.timeline-item:not(:last-child)::after {
  content: '';
  position: absolute;
  top: 24px;
  left: 16px;
  width: 2px;
  height: calc(100% + 1rem);
  background: rgba(255, 255, 255, 0.1);
}

.timeline-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: rgba(108, 92, 231, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(108, 92, 231, 0.3);
  z-index: 1;
  flex-shrink: 0;
}

.timeline-icon svg {
  width: 18px;
  height: 18px;
  fill: var(--primary-color);
}

.timeline-content {
  flex: 1;
}

.timeline-content h3 {
  font-size: 0.9rem;
  color: var(--text-muted);
  margin-bottom: 0.5rem;
}

.timeline-content p {
  font-size: 1rem;
  color: var(--text-color);
}

.winner-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 120px;
}

.winner-address {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  text-align: center;
}

.winner-icon {
  width: 48px;
  height: 48px;
  fill: var(--secondary-color);
}

.winner-btn {
  padding: 0.75rem 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-color);
  border-radius: var(--border-radius);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: var(--transition);
}

.winner-btn:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.1);
}

/* 页脚样式 */
.app-footer {
  background: rgba(18, 20, 36, 0.9);
  backdrop-filter: blur(10px);
  padding: 1.5rem;
  text-align: center;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.app-footer p {
  color: var(--text-muted);
  font-size: 0.9rem;
}

.footer-links {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.footer-links a {
  color: var(--text-muted);
  text-decoration: none;
}


.footer-links a:hover {
  color: var(--accent-color);
}


.country-display {
  color: var(--text-muted);
  font-size: 0.8rem;
}

/* 响应式样式 */
@media (max-width: 768px) {
  .app-header {
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
  }
  
  .wallet-connect {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  
  .lottery-overview,
  .lottery-details {
    grid-template-columns: 1fr;
  }
  
  .amount {
    font-size: 2rem;
  }
  
  .connect-prompt {
    padding: 2rem 1rem;
    margin: 2rem auto;
  }
  
  .connect-illustration {
    width: 80px;
    height: 80px;
  }
  
  .connect-prompt h2 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .account-info {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.5rem;
  }
  
  .account-badge, .country-badge {
    padding: 0.4rem 0.7rem;
    font-size: 0.8rem;
  }
  
  .wallet-icon-sm {
    width: 14px;
    height: 14px;
  }
  
  .prize-amount {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .eth-icon {
    margin-right: 0;
  }
  
  .action-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .app-footer {
    padding: 1rem;
  }
}

.text-btn {
  background: transparent;
  color: var(--accent-color);
  padding: 0;
  font-size: 1rem;
}

/* 添加超小屏幕适配 */
@media (max-width: 360px) {
  .app-header {
    padding: 0.8rem;
  }
  
  .logo h1 {
    font-size: 1.2rem;
  }
  
  .logo-icon {
    width: 28px;
    height: 28px;
  }
  
  .account-info {
    justify-content: center;
  }

  .address {
    max-width: 80px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
} 